<template>
    <div class="box"> 
      <h3>Araç Güncelle</h3>
      <form @submit.prevent="onUpdateForm">
 
      <div class="form-group">
          <input
            type="text"
            class="form-control"
            placeholder="Araç İsmi"
            v-model="arac.arac_ismi"
            required
          />
        </div>

         <div class="form-group">
          <input
            type="text"
            class="form-control"
            placeholder="Araç Durmu"
            v-model="arac.arac_durumu"
            required
          />
        </div> 


        <div class="form-group">
          <button class="btn btn-update">Güncelle</button>
        </div>
      </form>
    </div>

</template>

<script>
import { db } from "../firebaseDb";
export default {
  data() {
    return {
      arac: {},
    };
  },
  created() {
    let dbRef = db.collection("araclar").doc(this.$route.params.id);
    dbRef
      .get()
      .then((doc) => {
        this.arac = doc.data();
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    onUpdateForm(event) {
      event.preventDefault();
      db.collection("araclar")
        .doc(this.$route.params.id)
        .update(this.arac)
        .then(() => {
          console.log("User successfully updated!");
          this.$router.push("/araclist");
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>